@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap);
*{box-sizing:border-box;margin:0}body{font-family:'Inter', sans-serif;font-size:16px;font-weight:normal;font-style:normal;color:#1C1C21}.menu{position:relative;display:flex;align-items:center;justify-content:center;width:10vw;height:5vw;padding:0;border:none;background-color:transparent}.menu span{width:100%;height:2px;background-color:#000}.menu span::before,.menu span::after{content:"";position:absolute;left:0;width:100%;height:2px;background-color:#000;transition:all 0.3s linear}.menu span::before{transform:translateY(-2vw)}.menu span::after{transform:translateY(2vw)}.menu--active>span{height:0}.menu--active>span::before{transform:translateY(0) rotate(45deg)}.menu--active>span::after{transform:translateY(0) rotate(-45deg)}

.main-page{display:flex;align-items:center;width:100%;height:100vh;padding-left:5%;background:linear-gradient(to bottom right, #fff 50%, #FFF3EB 50%)}@media screen and (max-width: 480px){.main-page{padding:48px 16px;align-items:stretch}}@media (min-width: 481px) and (max-width: 1024px){.main-page{padding:0 5%}}.main-page__inner{display:grid;grid-template-columns:minmax(300px, 1fr) 1fr;grid-auto-rows:auto;align-items:center;width:100%}@media screen and (max-width: 480px){.main-page__inner{grid-template-columns:1fr;grid-template-rows:1fr 1fr}}.main-page__images{position:relative;display:flex;align-items:center;justify-content:center}.main-page__logo{width:100%;height:100%}.main-page__play{display:flex;flex-direction:column;justify-content:space-between;height:80%}@media (max-width: 480px){.main-page__play{align-items:center;text-align:center;height:100%}}.main-page__play h1{font-style:normal;font-weight:600;font-size:4vw;line-height:116%}@media (max-width: 480px){.main-page__play h1{font-size:32px}}.main-page__play-btn{display:flex;align-items:center;justify-content:center;width:44%;height:5vw;text-decoration:none;color:#fff;font-style:normal;font-weight:600;font-size:2vw;line-height:116%;background:#FF8B37;border-radius:12px;transition:all 0.3s linear}@media (max-width: 480px){.main-page__play-btn{width:100%;height:16vw;font-size:5vw}}.main-page__play-btn:hover{background-color:#FFAC70}

.game-page{display:grid;grid-template-columns:3fr 1fr;width:100%;height:100vh}@media screen and (max-width: 767px){.game-page{grid-template-columns:1fr}}@media (min-width: 768px) and (max-width: 1023px){.game-page{grid-template-columns:2fr 1fr}}.game-page__container-game{display:flex;flex-direction:column;justify-content:space-between;padding:5vw;background-color:#F5F5F7}@media screen and (max-width: 480px){.game-page__container-game{padding:5vw 0}}@media (min-width: 768px) and (max-width: 1024px){.game-page__container-game{display:grid;grid-template-rows:1fr 1fr;align-items:center}}.game-page__question{width:70%}@media screen and (max-width: 480px){.game-page__question{width:100%;padding:0 16px;text-align:center}}@media screen and (max-width: 1024px){.game-page__question{justify-self:center;width:100%;text-align:center}}.game-page__question p{font-style:normal;font-weight:600;font-size:3vw;line-height:116%}@media screen and (max-width: 480px){.game-page__question p{font-size:18px}}.game-page__answer{display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:10px}@media screen and (max-width: 480px){.game-page__answer{grid-template-columns:1fr}}@media (min-width: 768px) and (max-width: 1023px){.game-page__answer{grid-template-columns:1fr}}.game-page__answer svg{cursor:pointer;display:flex;width:100%;font-size:20px;line-height:116%}@media screen and (max-width: 480px){.game-page__answer svg{width:100%}}@media screen and (min-width: 1025px){.game-page__answer svg:hover path{stroke:#FF8B37}}.game-page__answer--select path{stroke:#FF8B37;fill:#FFF3EB}.game-page__answer--correct path{stroke:#47D867;fill:#E6FAEA}.game-page__answer--wrong path{stroke:#EC6259;fill:#FDEEED}.game-page__container-total{display:grid;grid-row-end:span;align-content:center;grid-auto-rows:-webkit-max-content;grid-auto-rows:max-content;grid-row-gap:10px}.game-page__container-total-1{grid-row:1}.game-page__container-total-2{grid-row:2}.game-page__container-total-3{grid-row:3}.game-page__container-total-4{grid-row:4}.game-page__container-total-5{grid-row:5}.game-page__container-total-6{grid-row:6}.game-page__container-total-7{grid-row:7}.game-page__container-total-8{grid-row:8}.game-page__container-total-9{grid-row:9}.game-page__container-total-10{grid-row:10}.game-page__container-total-11{grid-row:11}.game-page__container-total svg{width:100%}@media screen and (max-width: 767px){.game-page__container-total{position:absolute;width:100%;height:100%;align-content:end;grid-row-gap:5px;padding-bottom:10vw;background:#F5F5F7;transform:translateY(-100%);transition:all 0.6s linear}.game-page__container-total--active{transform:translateY(0)}}.game-page__container-total--current-money path,.game-page__container-total--current-money text{stroke:#FF8B37}.game-page__container-total--finished-money path,.game-page__container-total--finished-money text{stroke:#D0D0D8}.game-page__inner-menu{display:none}@media screen and (max-width: 767px){.game-page__inner-menu{z-index:10;display:flex;justify-content:flex-end;padding:0 20px}}

.game-over{display:flex;align-items:center;width:100%;height:100vh;padding-left:5%;background:#F5F5F7}@media screen and (max-width: 480px){.game-over{padding:48px 16px;align-items:stretch}}@media (min-width: 481px) and (max-width: 1024px){.game-over{padding:0 5%}}.game-over__inner{display:grid;grid-template-columns:minmax(300px, 1fr) 1fr;align-items:center;width:100%}@media screen and (max-width: 480px){.game-over__inner{grid-template-columns:1fr;grid-template-rows:1fr 1fr}}.game-over__images{position:relative;display:flex;align-items:center;justify-content:center}.game-over__logo{width:100%;height:100%}.game-over__play{display:flex;flex-direction:column;justify-content:space-between;height:80%}@media (max-width: 480px){.game-over__play{align-items:center;text-align:center;height:100%}}.game-over__play h1,.game-over__play p{font-style:normal;font-weight:600;line-height:116%}.game-over__play h1{font-size:4vw}@media (max-width: 480px){.game-over__play h1{font-size:32px}}.game-over__play p{font-size:3vw;opacity:0.5}@media (max-width: 480px){.game-over__play p{font-size:18px}}.game-over__play-btn{display:flex;align-items:center;justify-content:center;width:44%;height:5vw;text-decoration:none;color:#fff;font-style:normal;font-weight:600;font-size:2vw;line-height:116%;background:#FF8B37;border-radius:12px;transition:all 0.3s linear}@media (max-width: 480px){.game-over__play-btn{width:100%;height:16vw;font-size:5vw}}.game-over__play-btn:hover{background-color:#FFAC70}

